@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh; 
  background-color: #f1f1f1;
}

nav {
  height: auto;
}

.div-header {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  }

.div-logo {
  cursor: pointer;
  background: white;
  border-radius: 10%;
  position: absolute;
  top: 0.5rem;
  left: 0.5rem;
  margin: 0;
  padding: 0;
  z-index: 10;
  box-shadow: rgb(0 0 0 / 40%) 0px 1px 4px -1px;
}
.filter-container {
  display: flex;
  position: absolute;
  top: 8rem;
  left: 1.6rem;
  flex-direction: row;
}

.liveButton {
  display: flex;
  flex-direction: row;
  align-items: center;
  color: rgb(92, 92, 92);
  background: rgb(255, 255, 255);
  font-family: Arial;
  font-size: 18px;
  box-shadow: rgb(0 0 0 / 40%) 0px 1px 4px -1px;
  border-radius: 20% 0 0 20%;
  float: left;
  cursor: pointer;
  transition-duration: 0.2s;
  border: none;
}

.liveButton img {
  margin: 5px 5px 5px 2px;
  float: left;
  width: 20px;
}

.liveButton:hover {
  background-color: rgb(22, 193, 243);
  color: rgb(255, 255, 255);
}

.plannedButton {
  display: flex;
  flex-direction: row;
  align-items: center;
  color: rgb(92, 92, 92);
  background: rgb(255, 255, 255);
  font-family: Arial;
  font-size: 18px;
  box-shadow: rgb(0 0 0 / 40%) 0px 1px 4px -1px;
  border-radius: 0 0% 0% 0;
  float: left;
  cursor: pointer;
  transition-duration: 0.2s;
  border: none;
}

.plannedButton img {
  margin: 5px 5px 5px 2px;
  float: left;
  width: 20px;
}

.plannedButton:hover {
  background-color: rgb(22, 193, 243);
  color: rgb(255, 255, 255);
}

.showAllButton {
  background: rgb(255, 255, 255);
  font-size: 18px;
  box-shadow: rgb(0 0 0 / 40%) 0px 1px 4px -1px;
  border-radius: 0 20% 20% 0;
  cursor: pointer;
  transition-duration: 0.2s;
  border: none;
  color: rgb(92, 92, 92);
  padding: 10px;
}

.showAllButton:hover {
  background-color: rgb(22, 193, 243);
  color: #ffffff;
}

.searchBox {
  position: absolute;
  padding: 0px 40px 0px 0px;
  top: 0.5rem;
  left: 50%;
  transform: translateX(-50%);
  z-index: 11;
  background: white;
  box-shadow: rgb(0 0 0 / 40%) 0px 1px 4px -1px;
  border-radius: 10px;
  border: none;
}

.searchBox input {
  padding: 10px 20px;
  font-size: 20px;
  border-radius: 10px;
  border: none;
}

.searchImgButton {
  position: absolute;
  top: 0.45rem;
  left: 18rem;
  border: none;
  cursor: pointer;
  background: none;
}

.findLocationButton {
  position: absolute;
  top: 8.23rem;
  left: 15.8rem;
  z-index: 10;
  background: white;
  box-shadow: rgb(0 0 0 / 40%) 0px 1px 4px -1px;
  border-radius: 20%;
  border: none;
  cursor: pointer;
  transition-duration: 0.2s;
}

.findLocationButton img {
  margin: 2px 0px 2px 0px;
  float: left;
}

.findLocationButton:hover {
  background-color: rgb(22, 193, 243);
}

.report-container {
  display: flex;
  flex-direction: column;
  grid-row-gap: 10px;
  row-gap: 10px;
}

.reportPowerOutage {
  color: #fff;
  background-color: #7ac29a;
  font-size: 15px;
  border-radius: 50px;
  padding: 5px;
  border: none;
  cursor: pointer;
  transition-duration: 0.2s;
}

.reportPowerOutage:hover {
  background-color: #16bd61;
}

.reportNetworkHazard {
  color: #fff;
  background-color: #6d6b68;
  font-size: 15px;
  border-radius: 50px;
  padding: 5px;
  border: none;
  cursor: pointer;
  transition-duration: 0.2s;
}

.reportNetworkHazard:hover {
  background-color: #383736;
}

.cancelReport {
  color: #fff;
  background-color: #e97c50;
  font-size: 15px;
  border-radius: 50px;
  padding: 5px;
  border: none;
  cursor: pointer;
  transition-duration: 0.2s;
}

.cancelReport:hover {
  background-color: #d44b14;
}

.savePrefferedLocation {
  color: #fff;
  background-color: rgb(89, 215, 253);
  font-size: 15px;
  border-radius: 50px;
  padding: 5px;
  border: none;
  cursor: pointer;
  transition-duration: 0.2s;
}

.savePrefferedLocation:hover {
  background-color: rgb(0, 195, 255);
}

.locationSaved {
  background-color: rgb(182, 255, 182);
  color: rgb(26, 156, 26);
  font-weight: bold;
  padding: 0.5rem;
  margin-bottom: 0.5rem;
  text-align: center;
}

.locationNotSaved {
  background-color: lightpink;
  color: firebrick;
  font-weight: bold;
  padding: 0.5rem;
  margin-bottom: 0.5rem;
  text-align: center;
}

.horizontal {
  background-color: black;
  height: 20px;
  width: 3px;
}

.info-window {
  display: flex;
  grid-gap: 5px;
  gap: 5px;
  flex-direction: column;
  font-family: Arial;
}

.img-txt {
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 5px;
  gap: 5px;
  font-family: Arial, sans-serif;
  font-weight: 400;
  font-size: 15px;
  max-width: 25rem;
}

.LogIn-container {
  display: flex;
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  flex-direction: row;
}

.LogIn {
  display: flex;
  flex-direction: row;
  background: rgb(255, 255, 255);
  box-shadow: rgb(0 0 0 / 40%) 0px 1px 4px -1px;
  border-radius: 20% 0% 0% 20%;
  cursor: pointer;
  transition-duration: 0.2s;
  border: none;
  color: rgb(22, 193, 243);
  font-family: Arial;
  font-size: 18px;
  align-items: center;
  padding: 10px 5px 10px 10px;
}

.LogIn:hover {
  background-color: rgb(22, 193, 243);
  color: rgb(255, 255, 255);
}

.SignUp {
  display: flex;
  flex-direction: row;
  background: rgb(255, 255, 255);
  box-shadow: rgb(0 0 0 / 40%) 0px 1px 4px -1px;
  border-radius: 0% 20% 20% 0%;
  cursor: pointer;
  transition-duration: 0.2s;
  border: none;
  color: rgb(22, 193, 243);
  font-family: Arial;
  font-size: 18px;
  align-items: center;
  padding: 10px 10px 10px 5px;
}

.SignUp:hover {
  background-color: rgb(22, 193, 243);
  color: rgb(255, 255, 255);
}

.menu-container {
  display: flex;
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  flex-direction: column;
}

.menu {
  background: rgb(255, 255, 255);
  box-shadow: rgb(0 0 0 / 40%) 0px 1px 4px -1px;
  border-radius: 20% 20% 20% 20%;
  cursor: pointer;
  transition-duration: 0.2s;
  border: none;
  color: rgb(22, 193, 243);
  font-family: Arial;
  font-size: 18px;
  padding: 10px 15px 10px 15px;
}

.menu:hover {
  background-color: rgb(22, 193, 243);
  color: rgb(255, 255, 255);
}

.showLocations {
  background: rgb(255, 255, 255);
  box-shadow: rgb(0 0 0 / 40%) 0px 1px 4px -1px;
  border-radius: 20% 20% 0% 0%;
  cursor: pointer;
  transition-duration: 0.2s;
  border: none;
  color: rgb(22, 193, 243);
  font-family: Arial;
  font-size: 18px;
  padding: 10px 10px 10px 10px;
}

.showLocations:hover {
  background-color: rgb(22, 193, 243);
  color: rgb(255, 255, 255);
}

.LogOut {
  background: rgb(255, 255, 255);
  box-shadow: rgb(0 0 0 / 40%) 0px 1px 4px -1px;
  border-radius: 0% 0% 20% 20%;
  cursor: pointer;
  transition-duration: 0.2s;
  border: none;
  color: rgb(22, 193, 243);
  font-family: Arial;
  font-size: 18px;
  padding: 10px 10px 10px 10px;
}

.LogOut:hover {
  background-color: rgb(22, 193, 243);
  color: rgb(255, 255, 255);
}

.locationsTable {
  display: flex;
  flex-direction: column;
  z-index: 11;
  position: absolute;
  top: 9rem;
  right: 0.5rem;
  background: white;
  border-radius: 8px;
  box-shadow: 0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%);
  padding: 15px;
  color: rgb(22, 193, 243);
  align-items: center;
  font-family: Arial;
  font-size: 18px;
  cursor:default;
}

td {
  padding: 5px 10px;
}

.deleteLocation {
  border: solid 1px;
  border-radius: 8px;
  cursor: pointer;
  background: rgb(255, 255, 255);
  color: rgb(119, 214, 243);
}

.deleteLocation:hover {
  background-color: rgb(22, 193, 243);
  color: rgb(255, 255, 255);
}

.loginSection {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: 352px;
    min-height: 380px;
    background-color: rgb(255, 255, 255);
    border-radius: 8px;
    box-shadow: 0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%);
    border: none;
    padding: 24px 0px;
    z-index: 11;
    position: absolute;
    top: 9rem;
    left: 50%;
    transform: translateX(-50%);
}

form {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    flex-grow: 1;
    line-height: 1.5;
    max-width: 300px;
}

.signInButton {
    background: rgb(22, 193, 243);
    color: #fff;
    border: none;
    border-radius: 28px;
    font-size: 1.2rem;
    line-height: 48px;
    padding: 0 16px;
    width: 332px;
    cursor: pointer;
    max-width: 300px;
}

input[type="text"],
input[type="password"],
textarea {
    font-family: 'Nunito', sans-serif;
    font-size: 20px;
    display: inline-block;
    padding: 10px 15px;
    border: 0.1px solid rgb(151, 151, 151);
}

.instructions {
    font-size: 0.75rem;
    border-radius: 0.5rem;
    background: #000;
    color: #fff;
    padding: 0.25rem;
    position: relative;
    bottom: -10px;
}

.instructions>svg {
    margin-right: 0.25rem;
}

.offscreen {
    position: absolute;
    left: -9999px;
}

.hide {
    display: none;
}

.valid {
    color: limegreen;
    margin-left: 0.25rem;
}

.invalid {
    color: red;
    margin-left: 0.25rem;
}

.errmsg {
    background-color: lightpink;
    color: firebrick;
    font-weight: bold;
    padding: 0.5rem;
    margin-bottom: 0.5rem;
}

.loginHeader {
    font-size: 2.2rem;
    line-height: 1.25;
    font-weight: 600;
    color: rgba(0, 0, 0, 0.9);
    padding: 0 0 4px 0;
}
